<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-alert title=":" type="warning" show-icon :closable="false"></el-alert>
      <!--  -->
      <el-row class="cat_opt">
        <el-col>
          <span>:</span>
          <!--  -->
          <el-cascader
            :options="cateList"
            v-model="selectedOptions"
            :props="{value:'cat_id',label:'cat_name',children:'children', expandTrigger: 'hover'}"
            @change="handleChange"
            clearable
          ></el-cascader>
        </el-col>
      </el-row>
      <!-- tab -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="many">
          <el-button type="primary" size="mini" :disabled="isBtnNeedForbidden"></el-button>
          <el-table :data="manyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand"></el-table-column>
            <!--  -->
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template>
                <el-button size="mini" type="primary" icon="el-icon-edti"></el-button>
                <el-button size="mini" type="danger" icon="el-icon-delete"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="only">
          <el-button type="primary" size="mini" :disabled="isBtnNeedForbidden"></el-button>
          <!--  -->
          <el-table :data="onlyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand"></el-table-column>
            <!--  -->
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template>
                <el-button size="mini" type="primary" icon="el-icon-edti"></el-button>
                <el-button size="mini" type="danger" icon="el-icon-delete"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      cateList: [],
      selectedOptions: [], //
      activeName: 'many', //tab
      manyTableData: [], //
      onlyTableData: [] // 
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    async getCateList() {
      let { data: res } = await this.$axios.get('categories')
      if (res.meta.status !== 200)
        return this.$message.error('')
      this.cateList = res.data
    },
    // 
    handleChange() {
      this.getParamsData()
    },
    // tab
    handleClick() {
      this.getParamsData()
    },
    // 
    async getParamsData() {
      if (this.selectedOptions.length !== 3) return
      let { data: res } = await this.$axios.get(
        `categories/${this.cateId}/attributes`,
        {
          params: { sel: this.activeName }
        }
      )
      if (res.meta.status !== 200)
        return this.$message.error('')
      if (this.activeName === 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    }
  },
  computed: {
    //  true
    isBtnNeedForbidden() {
      if (this.selectedOptions.length !== 3) {
        return true
      }
      return false
    },
    // id
    cateId() {
      if (this.selectedOptions.length === 3) {
        return this.selectedOptions[2]
      }
      return null
    }
  }
}
</script>
<style lang="less" scoped>
.cat_opt {
  margin-top: 15px;
}
</style>